import * as React from 'react';
import { css } from 'office-ui-fabric-react';
import styles from './Zion.module.scss';
import { IZionProps } from './IZionProps';

export default class Zion extends React.Component<IZionProps, void> {
  public render(): React.ReactElement<IZionProps> {
    return (
      <div className={styles.helloWorld}>
        <div className={styles.container}>
          <div className={css('ms-Grid-row ms-bgColor-themeDark ms-fontColor-white', styles.row)}>
            <div className='ms-Grid-col ms-u-lg10 ms-u-xl8 ms-u-xlPush2 ms-u-lgPush1'>
              <span className='ms-font-xl ms-fontColor-white'>
                {this.props.title}
              </span>
              <p className='ms-font-l ms-fontColor-white'>
                Customize SharePoint experiences using Web Parts.
              </p>
              <p className='ms-font-l ms-fontColor-white'>
                {this.props.description}
                
              </p>
              <a className={css('ms-Button', styles.button)}
                 href={this.props.url}>
                <span className='ms-Button-label'>Learn more</span>
              </a>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
